.section {
  padding-top: 15px;
  padding-bottom: 15px;
}

.list {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-top: 25px;
  margin-bottom: 25px;

  @media screen and (min-width: 1279px) {
    gap: 54px;
  }
}

.logo {
  position: absolute;
  left: 20px;
  top: 20px;
}

.item {
  position: relative;
  padding-top: 265px;
  width: 320px;
  height: 460px;
  background: url('../../images/picture2.png') no-repeat,
    linear-gradient(115deg, #471ca9 -1%, #5736a3 54%, #4b2a99 79%);
  box-shadow: -2.5px 6.8px 20.6px rgba(0, 0, 0, 0.23);
  border-radius: 20px;
  background-position-x: center;
  background-position-y: 10%;

  @media screen and (min-width: 768px) {
    width: 360px;
  }
  @media screen and (min-width: 1279px) {
    width: 380px;
  }
}

.middleLine {
  position: absolute;
  width: 320px;
  height: 8px;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);

  background: #ebd8ff;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.06), inset 0px -1.7px 3px #ae7be3,
    inset 0px 3px 2.5px #fbf8ff;

  @media screen and (min-width: 768px) {
    width: 360px;
  }

  @media screen and (min-width: 1279px) {
    width: 380px;
  }
}

.avatarContainer {
  position: absolute;
  width: 80px;
  height: 80px;
  top: 47%;
  left: 50%;
  transform: translate(-50%, -50%);

  background: #ebd8ff;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.06), inset 0px -2px 4px #ae7be3,
    inset 0px 4px 3px #fbf8ff;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  border-radius: 85px;
}

.avatarBack {
  position: absolute;
  width: 62px;
  height: 62px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  background: #5736a3;
  border-radius: 85px;
  overflow: hidden;
}

.image {
  width: 100%;
}

.userTitle {
  font-size: 16px;
  line-height: 1.2;
  color: #ebd8ff;
  text-align: center;
}

.text {
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2;
  text-transform: uppercase;
  color: #ebd8ff;

  text-align: center;
  margin-top: 15px;
}

.buttonFollow {
  display: block;
  padding: 14px 28px;
  width: 195px;
  height: 50px;

  background: #ebd8ff;
  box-shadow: 0px 3.4px 3.4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;

  font-weight: 600;
  font-size: 18px;
  line-height: 1.22;
  text-transform: uppercase;
  color: #373737;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.buttonFollow:hover,
.buttonFollow:focus {
  background-color: #dbc0f7;
}

.button,
.buttonBack {
  display: block;
  padding: 14px 14px;
  
  height: 50px;

  background-color: #471ca9;
  box-shadow: 0px 3.4px 3.4px rgba(0, 0, 0, 0.3);
  border-radius: 10px;

  margin-left: auto;
  margin-right: auto;

  text-transform: uppercase;
  color: #ffffff;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.buttonBack {
  margin-left: 0;
}

.button:hover,
.button:focus,
.buttonBack:hover,
.buttonBack:focus {
  background-color: #380aa1;
}
